import { ImageComponent } from 'components';
import React from 'react';
import { ImagesPreview, ImageContainer } from './styled';
const Component = (images) => {

    const loadImages = () => images.map((image, index) => <ImageContainer key={`image-${index}`} onClick={() => { console.log(image) }}><ImageComponent src={image} /></ImageContainer>)
    return (
        <ImagesPreview>
            <ImageComponent>
                {loadImages}
            </ImageComponent>
        </ImagesPreview>

    );
}

export default Component;
export { Component as ImagesPreview }